<template lang="html">
  <div class="bad-comment-detail-page">
    <!-- 头部 -->
    <default-head header="差评待处理"></default-head>
    <light-title text="我收到的差评"></light-title>

    <div class="bad-comment-box"
      v-infinite-scroll="loadMore"
      :infinite-scroll-disabled="loading"
      :infinite-scroll-distance="10"
      :infinite-scroll-immediate-check="true"
    >
      <bad-comment-list v-for="(item, index) in receiveComment" :key="item.index" @left-button="confirm(item)" @right-button="appeal(item)" :isReceive=true :class="{borderB: index !== receiveComment.length-1}" :star="item.score" :mobile="item.mobile" :tagName="item.tagName" :time="item.updateTime"></bad-comment-list>
      <no-data v-if="!this.receiveComment.length"></no-data>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import _ from 'underscore'
import minutils from '@/utils/minUtils'
import noData from '@/components/common/noData'
import defaultHead from '@/components/head/defaultHead'
import lightTitle from '@/components/common/lightTitle'
import badCommentList from '@/components/badcomment/badCommentList'

export default {
  data () {
    return {
      loading: false,
      pageSize: 11,
      pageIndex: 1,
      pages: 1,
      receiveComment: []
    }
  },
  computed: {
    userId: function () {
      return utils.userId()
    }
  },
  mounted () {
    this.loadMore()
  },
  methods: {
    loadMore: _.throttle(function () {
      // 关闭无线滚动
      this.loading = true
      if (this.pageIndex === 1) {
        this.getCommentReceive(this.userId, 1, this.pageIndex++, this.pageSize)
      } else if ((this.pageIndex - 1) * this.pageSize > this.pages) {
        return
      } else {
        this.getCommentReceive(this.userId, 1, this.pageIndex++, this.pageSize)
      }
    }, 300),
    getCommentReceive (userId, level, pageIndex, pageSize) {
      minutils.IndicatorOpen('加载中')
      // 修改参数
      api.getCommentByList({userId: userId, level: level, pageIndex: pageIndex, pageSize: pageSize}).then((response) => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          this.pages = result.data.total
          this.receiveComment = [...this.receiveComment, ...result.data.list]
        }
      })
    },
    confirm (item) {
      minutils.MessageBoxConfirm({
        message: '您确认签收该差评吗？',
        title: '提示',
        options: {
          confirmButtonText: '签收差评',
          cancelButtonText: '不签收'
        },
        success (action) {
          // 调用接口签收
        }
      })
    },
    appeal (item) {
      sessionStorage.setItem('appealform', JSON.stringify(item))
      this.$router.push('/appealform')
    }
  },
  components: { defaultHead, lightTitle, badCommentList, noData }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.bad-comment-detail-page {
  padding-top: .88rem;

}

</style>



