<template lang="html">
  <div class="bad-comment-list">
    <div class="bad-comment-item">
      <p class="bad-comment-item__title" v-if="isReceive">客户({{mobile}})给了我<span class="tagName" v-if="false">{{tagName}}</span>{{this.starNum}}颗星差评</p>
      <p class="bad-comment-item__title" v-else>您给了商家({{mobile}})<span class="tagName" v-if="false">{{tagName}}</span>{{this.starNum}}颗星差评</p>
      <div class="bad-comment-item__detail">
        <span class="bad-comment-button" @click="leftButton()" v-if="false">{{leftText}}</span>
        <span class="bad-comment-button" @click="rightButton()" v-if="false">{{rightText}}</span>
        <span class="bad-comment-time">{{time}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    leftText: {
      type: String,
      default: '签收差评'
    },
    rightText: {
      type: String,
      default: '差评申诉'
    },
    mobile: {
      type: String,
      default: ''
    },
    tagName: {
      type: String,
      default: ''
    },
    star: {
      type: String | Number,
      default: '1'
    },
    time: {
      type: String | Number,
      default: ''
    },
    isReceive: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    starNum: function () {
      return this.star === 1 ? '一' : '二'
    }
  },
  methods: {
    leftButton () {
      this.$emit('left-button')
    },
    rightButton () {
      this.$emit('right-button')
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.bad-comment-item {
  height: 1.56rem;
  padding: 0 .22rem .16rem;
  font-size: .3rem;
  position: relative;
  background: @whiteColor;
  .bad-comment-item__title {
    line-height: .7rem;
    color: @defaultColor;
    .tagName {
      color: red;
      margin: 0 .1rem;
    }
  }
  .bad-comment-item__detail {
    .bad-comment-button {
      display: inline-block;
      width: 1.6rem;
      height: .68rem;
      line-height: .68rem;
      text-align: center;
      border-radius: .05rem;
      background: @activeColor;
      color: @whiteColor;
      &:nth-child(2) {
        margin-left: .3rem;
      }
    }
    .bad-comment-time {
      font-size: .24rem;
      line-height: .24rem;
      position: absolute;
      right: .22rem;
      bottom: .16rem;
      color: @lightColor;
    }
  }
}
</style>



