<template lang="html">
  <div class="no-data">
    <div class="pic-box" :style="{'top': notop}">
      <img src="../../assets/img/404-2@2x.png" alt="">
    </div>
    <p class="text">{{message}}</p>
  </div>
</template>

<script>
export default {
  props: {
    message: {
      type: String,
      default: '暂无任何数据'
    },
    top: {
      type: String,
      default: '1.5rem'
    }
  },
  watch: {
    top (val) {
      this.notop = val
    }
  },
  data () {
    return {
      notop: this.top
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

  .no-data {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .pic-box {
      width: 2.43rem;
      height: 2.65rem;
      // background: tan;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .text {
      font-size: .3rem;
      padding-top: .4rem;
      // padding-left: .5rem;
      text-align: center;
      color: @lightColor;
    }
  }
</style>



